<script>
	export default {
		props:{
			tabTwoPages:{
				type:Array,
				required:true,
			},
		},
		methods: {

			//-------------------------------------------------------------------------------------
			/*
				
			 */
			editInstructions(){
				this.navigation = true;
				this.preview = false;
				this.edit = true;
			},
			/*
				
			 */
			previewInstructions(){
				this.navigation = true;
				this.preview = true;
				this.edit = false;
			},
			/*
				
			 */
			save(value,render){
				let form = {
					instructions:value,
				}
				this.axios.patch(`${this.common.basePath}/project/${this.activeIndex}`,form).then( (response) => {
					if(response.data.meta.code === 0){
						this.$message.success("", 20);
						this.preview = true;
