			// @imgAdd event
      $imgAdd(pos, $file){
				// ..
				var formdata = new FormData();
				formdata.append('file', $file);
				this.axios({
				  url: `${this.common.basePath}/file/uplode`,
				  method: 'post',
				  data: formdata,
				  headers: { 'Content-Type': 'multipart/form-data' },
				}).then((url) => {
				  // .url![...](0) -> ![...](url)
			   /**
			   * $vm mavonEditor
			   * 1. : `import {mavonEditor} from ...` `$vm``mavonEditor`
			   * 2. $refs: htmlref : `<mavon-editor ref=md ></mavon-editor>`$vm` `this.$refs.md`
