					{	pattern: /^([0-9]|[1-9]\d{1,3}|[1-5]\d{4}|6[0-4]\d{4}|65[0-4]\d{2}|655[0-2]\d|6553[0-5])$/, message: '' },]">
						<el-input v-model="addForm.ngnix_port_code"></el-input>
