					</el-form-item>
					<el-form-item
						prop="ngnix_one_description"
						label=""
						:rules="[{ required: true, message: '', trigger: 'blur' },]">
						<el-input type="textarea" v-model="addForm.ngnix_one_description"></el-input>
					</el-form-item>
				</div>
				<div v-if="addTypeDialog=='5'||addTypeDialog=='8'"><!--  -->
					<el-form-item
						prop="databasers_list_name"
						label=""
						:rules="[{ required: true, message: '', trigger: 'blur' },]">
						<el-input v-model="addForm.databasers_list_name"></el-input>
					</el-form-item>
					<el-form-item
						prop="account"
						label=""
						:rules="[{ required: true, message: '', trigger: 'blur' },]">
						<el-input v-model="addForm.account"></el-input>
					</el-form-item>
					<el-form-item
						prop="password"
						label=""
						:rules="[{ required: true, message: '', trigger: 'blur' },]">
						<el-input v-model="addForm.password"></el-input>
					</el-form-item>
					<el-form-item
						prop="port_code"
						label=""
						:rules="[{ required: true, message: '', trigger: 'blur' },{	pattern: /^([0-9]|[1-9]\d{1,3}|[1-5]\d{4}|6[0-4]\d{4}|65[0-4]\d{2}|655[0-2]\d|6553[0-5])$/, message: '' },]">
						<el-input v-model="addForm.port_code"></el-input>
					</el-form-item>
					<el-form-item
						prop="parameters"
						label=""
						:rules="[{ required: true, message: '', trigger: 'blur' },]">
						<el-input v-model="addForm.parameters"></el-input>
					</el-form-item>
					<el-form-item
						prop="databses_list_description"
						label=""
						:rules="[{ required: true, message: '', trigger: 'blur' },]">
						<el-input type="textarea" v-model="addForm.databses_list_description"></el-input>
					</el-form-item>
				</div>
				<div v-if="addTypeDialog=='6'||addTypeDialog=='10'"><!-- redis -->
					<el-form-item
						prop="redis_list_name"
						label=""
						:rules="[{ required: true, message: '', trigger: 'blur' },]">
						<el-input v-model="addForm.redis_list_name"></el-input>
					</el-form-item>
					<el-form-item
						prop="redis_port_code"
						label=""
						:rules="[{ required: true, message: '', trigger: 'blur' },{	pattern: /^([0-9]|[1-9]\d{1,3}|[1-5]\d{4}|6[0-4]\d{4}|65[0-4]\d{2}|655[0-2]\d|6553[0-5])$/, message: '' },]">
						<el-input v-model="addForm.redis_port_code"></el-input>
					</el-form-item>
					<el-form-item
						prop="redis_list_parameters"
						label=""
						:rules="[{ required: true, message: '', trigger: 'blur' },]">
						<el-input v-model="addForm.redis_list_parameters"></el-input>
					</el-form-item>
					<el-form-item
						prop="redis_list_description"
						label=""
						:rules="[{ required: true, message: '', trigger: 'blur' },]">
						<el-input type="textarea" v-model="addForm.redis_list_description"></el-input>
					</el-form-item>
				</div>
				<div v-if="addTypeDialog=='7'||addTypeDialog=='14'"><!--  -->
					<el-form-item
						prop="port"
						label=""
						:rules="[{ required: true, message: '', trigger: 'blur' },{	pattern: /^([0-9]|[1-9]\d{1,3}|[1-5]\d{4}|6[0-4]\d{4}|65[0-4]\d{2}|655[0-2]\d|6553[0-5])$/, message: '' },]">
						<el-input v-model="addForm.port"></el-input>
					</el-form-item>
					<el-form-item
						prop="occupy_title"
						label=""
						:rules="[{ required: true, message: '', trigger: 'blur' },]">
						<el-input type="textarea" v-model="addForm.occupy_title"></el-input>
					</el-form-item>
				</div>
			</el-form>
			<span slot="footer" class="dialog-footer">
				<el-button type="primary" v-if="Number(addTypeDialog)>=8" @click="editSubmitForm('addForm',addTypeDialog)"> </el-button>
				<el-button type="primary" v-if="Number(addTypeDialog)<8" @click="addSubmitForm('addForm',addTypeDialog)"> </el-button>
				<el-button @click="resetForm('addForm')"> </el-button>
				<el-button @click="addDialogVisible = false"> </el-button>
			</span>
		</el-dialog>
		<!--  -->
		<el-dialog
			title=""
			:visible.sync="editDetailDialogVisible"
			width="750px">
			<el-form ref="serverForm" label-width="120px" :model="serverForm" label-position="" :rules="rules">
				<el-form-item label="" prop="name">
					<el-input v-model="serverForm.name"></el-input>
				</el-form-item>
				<el-form-item label="" prop="ser_number">
					<el-input v-model="serverForm.ser_number"></el-input>
				</el-form-item>
				<el-form-item label="" prop="position">
					<el-input type="textarea" v-model="serverForm.position" :rows="5"></el-input>
				</el-form-item>
				<el-form-item label="IP" prop="ser_ip">
					<el-input v-model="serverForm.ser_ip"></el-input>
				</el-form-item>
				<el-form-item label="" prop="sys_version">
					<el-input v-model="serverForm.sys_version"></el-input>
				</el-form-item>
				<el-form-item label="" prop="configure">
					<el-input type="textarea" v-model="serverForm.configure" :rows="5"></el-input>
				</el-form-item>
