			<span slot="footer" class="dialog-footer">
				<el-button @click="editDetailDialogVisible = false"> </el-button>
				<el-button type="primary" @click="editDetailForm('serverForm')"> </el-button>
			</span>
		</el-dialog>
	</div>
</template>

