					if(JSON.stringify(response.data.data)=='[]'){
						this.serverRedisDetail = null;//---Redis
					}else{
						this.serverRedisDetail = response.data.data;//---Redis
					}
				}else{
					this.$message.error(response.data.meta.message, 20);   
				}
				this.loading_redis_detail = false;
			}).catch( (error) => {
				this.$message.error(error);
				this.loading_redis_detail = false;
			});
		},
		/* 
			GET  --redisidRedis
		*/
		getServerRedisList(id){
			this.loading_redis_detail = true;
			this.axios.get(`${this.common.basePath}/redis_index`,{params:{redis_id:id}}).then( (response) => {
				if(response.data.meta.code === 0){
					if(JSON.stringify(response.data.data.rows)=='[]'){
						this.serverRedisList = null;//Redis
					}else{
						this.serverRedisList = response.data.data.rows;//Redis
					}
				}else{
					this.$message.error(response.data.meta.message, 20);   
				}
				this.loading_redis_detail = false;
			}).catch( (error) => {
				this.$message.error(error);
				this.loading_redis_detail = false;
			});
		},
		/* 
			redis
		*/
		showRedisDetail(id){//redis
			this.getServerRedisDetail(id);
			this.getServerRedisList(id);
			this.isShowRedisDetail = true;
		},
		/* 
			Redis
		*/
		//redis
		addRedis(type,id){
			
			this.addTypeDialog= type;
			this.addRedisId  = id;
			this.addDialogVisible= true;

			//
			if(this.$refs['addForm']){
				this.$refs['addForm'].resetFields();
			}
		},
		addRedis_(){
			let addForm = {
				redis_id:this.addRedisId,
				index:Number(this.addForm.index),
				description:this.addForm.description,
			};
			this.axios.post(`${this.common.basePath}/redis_index`,addForm).then( (response) => {
				if(response.data.meta.code === 0){
					this.$message.success("", 20);
					this.getServerRedisList(this.addRedisId);
					this.addDialogVisible = false;
				}else{
					this.$message.error(response.data.meta.message, 20);   
				}
			}).catch( (error) => {
				this.$message.error(error);
			});
		},
		//redis
		addRedisOne(type,id){
			
			this.addTypeDialog= type;
			// this.addRedisId  = id;
			this.addDialogVisible= true;

			//
			if(this.$refs['addForm']){
				this.$refs['addForm'].resetFields();
			}
		},
		addRedisOne_(){
			let addForm = {
				parameters:this.addForm.redis_list_parameters,
				name:this.addForm.redis_list_name,
				description:this.addForm.redis_list_description,
				port_code:this.addForm.redis_port_code,
				server_id:this.activeIndex,
			};
			this.axios.post(`${this.common.basePath}/redis`,addForm).then( (response) => {
				if(response.data.meta.code === 0){
					this.$message.success("", 20);
					this.getServerRedis();
					this.addDialogVisible = false;
				}else{
					this.$message.error(response.data.meta.message, 20);   
				}
			}).catch( (error) => {
				this.$message.error(error);
			});
		},
		/*
			redis
		 */
		//redis
		editRedis_(){
			let editForm = {
				redis_id:this.addRedisId,
				index:Number(this.addForm.index),
				description:this.addForm.description,
			};
			this.axios.patch(`${this.common.basePath}/redis_index/${this.editId}`,editForm).then( (response) => {
				if(response.data.meta.code === 0){
					this.$message.success("", 20);
					this.getServerRedisList(this.refushId);
					this.addDialogVisible = false;
				}else{
					this.$message.error(response.data.meta.message, 20);   
				}
			}).catch( (error) => {
				this.$message.error(error);
			});
		},
		//redis
		editRedisOne_(){
			let editForm = {
			  parameters:this.addForm.redis_list_parameters,
				name:this.addForm.redis_list_name,
				description:this.addForm.redis_list_description,
				port_code:this.addForm.redis_port_code,
				server_id:this.activeIndex,
			};
			this.axios.patch(`${this.common.basePath}/redis/${this.editId}`,editForm).then( (response) => {
				if(response.data.meta.code === 0){
					this.$message.success("", 20);
					this.getServerRedis();
					this.addDialogVisible = false;
				}else{
					this.$message.error(response.data.meta.message, 20);   
				}
			}).catch( (error) => {
				this.$message.error(error);
			});
		},
		
		//-------------------------------------------------------------------------------------
		/* 
			
		*/
		builtServer(){
			this.resetForm("serverForm");//
			this.isShowBuilt = !this.isShowBuilt;
			this.icon = this.icon == 'el-icon-plus'?'el-icon-back':'el-icon-plus';
			this.buildText = this.buildText == ''?'':'';
			this.buildText == ''?document.getElementById("btn-box_").style.cssFloat = 'right':document.getElementById("btn-box_").style.cssFloat = 'left';
			this.selectBox = !this.selectBox;
		},
		/* 
			
		*/
		cancel(){
			this.isShowBuilt = false;
			this.icon = 'el-icon-plus';
			this.buildText = '';
			document.getElementById("btn-box_").style.cssFloat = 'left'
			this.selectBox = true;
		},
		/* 
			POST 
		*/
		addServer(){
			this.axios.post(`${this.common.basePath}/server`,this.serverForm).then( (response) => {
				if(response.data.meta.code === 0){
					this.$message.success("", 20);

					this.isShowBuilt = false;
					this.icon = 'el-icon-plus';
					this.buildText = '';
					document.getElementById("btn-box_").style.cssFloat = 'left'
					this.selectBox = true;

					this.getServerList();
				}else{
					this.$message.error(response.data.meta.message, 20);   
				}
			}).catch( (error) => {
				this.$message.error(error);
			});
		},
		/* 
		
		*/
		submitForm(formName) {
			this.$refs[formName].validate((valid) => {
				if (valid) {
					this.addServer();
				} else {
					this.$message.error('', 20); 
					return false;
				}
			});
		},
		/* 
		
		*/
		resetForm(formName) {
			this.$refs[formName].resetFields();
		},
		
	},
	mounted(){ 
		/* 
		tabTwoPages
		*/
		let serverPages = [];
		for(var i = 0;i<this.tabTwoPages.length;i++){
			if(this.tabTwoPages[i].split("_")[0]=="server"){
				serverPages.push(this.tabTwoPages[i])
			}
		}
		this.serverPages = serverPages;
		this.activeName = serverPages[0];
		/* 
			GET  --
		*/
		this.getServerList();
	},
	data(){
		return {
			loading:false,//-
			loading_edit:false,//-
			loading_detail:false,//-
			loading_databases:false,//-
			loading_databases_detail:false,//--
			loading_redis:false,//-redis
			loading_redis_detail:false,//-redis-
			loading_port:false,//-
			loading_ngnix:false,//-ngnix
			loading_ngnix_detail:false,//-ngnix-
			tempAtId:null,//Id
			serverPages:[],//
			activeName:null,
			activeIndex:null,//id
			servertList:null,//
			defaultServer:null,//
			
			serverDetail:null,//---
			serverPort:null,//---
			serverDatabases:null,//---
			serverDatabasesDetail:null,//---
			serverDatabasesList:null,//
			serverNginx:null,//---nginx
			serverNgnixDetail:null,//---Ngnix
			serverNgnixList:null,//Ngnix
			serverRedis:null,//---redis
			serverRedisDetail:null,//---redis
			serverRedisList:null,//Redis
			
			editDetailDialogVisible:false,//
			selectBox:true,
			icon:'el-icon-plus',
			buildText:'',
			serverForm:{//
				name:null,
				configure: null,
				position: null,
				ser_ip: null,
				ser_number: null,
				sys_version: null,
			},
			rules:{
				name: [
					{ required: true, message: '', trigger: 'blur' },
				],
				ser_ip: [
					{ required: true, message: 'IP', trigger: 'blur' },
					{	pattern: /^(\d|[1-9]\d|1\d{2}|2[0-4]\d|25[0-5])\.(\d|[1-9]\d|1\d{2}|2[0-4]\d|25[0-5])\.(\d|[1-9]\d|1\d{2}|2[0-4]\d|25[0-5])\.(\d|[1-9]\d|1\d{2}|2[0-4]\d|25[0-5])$/, message: 'IP' },
				],
				position: [
					{ required: true, message: '', trigger: 'blur' },
					{ min: 1, max: 255, message: ' 1  255 ', trigger: 'blur' }
				],
				ser_number: [
					{ required: true, message: '', trigger: 'blur' },
					{ min: 1, max: 25, message: ' 1  25 ', trigger: 'blur' },
					{	pattern: /^[A-Za-z0-9]+$/, message: '' }
				],
				sys_version: [
					{ required: true, message: '', trigger: 'blur' },
					{ min: 1, max: 25, message: ' 1  25 ', trigger: 'blur' },
				],
				configure: [
					{ required: true, message: '', trigger: 'blur' },
					{ min: 1, max: 255, message: ' 1  255 ', trigger: 'blur' },
				],
			},
			
			isShowBuilt:false,//
			isShowNginxDetail:false,//nginx
			isShowRedisDetail:false,//redis
			isShowDataDetail:false,//
			
			//
			editId:null,//id
			refushId:null,//id
			addTitleArr:[
				'',//0
				'redis',//1
				'nginx',//2
				'nginx',//3
				'nginx',//4
				'',//5
				'redis',//6
				'',//7
				'',//8
				'',//9
				'redis',//10
				'redis',//11
				'ngnix',//12
				'ngnix',//13
				'',//14
			],
			addTypeDialog:null,
			addDialogVisible:false,
			addForm:{
				//
				name:null,
				purpose:null,
				creat_sql:null,
				databases_deploy_id:null,
				//redis
				redis_deploy_id:null,
				index:null,
				description:null,
				//ngnix
				match:null,
				agent:null,
				effect:null,
				//ngnix
				config_code:null,
				ngnix_one_description:null,
				ngnix_one_name:null,
				ngnix_port_code:null,
				//
				account:null,
				password:null,
				parameters:null,
				databasers_list_name:null,
				databses_list_description:null,
				port_code:null,
				//redis
				redis_list_parameters:null,
				redis_list_description:null,
				redis_list_name:null,
				redis_port_code:null,
				//
				occupy_title:null,
				port:null,
			},
			addDataBasesId:null,
			addRedisId:null,
			addNgnixId:null,//
			
		}
	},
};
</script> 


