<!-- 
	
 -->

<template>
  <div>
		<div class="system-container">
			<div class="left-tabs-box">
				<div class="tabs-cotent-box">
					<div class="box-title"></div>
					<el-form :inline="true" :model="formInline" class="demo-form-inline">
						<el-form-item label="">
							<el-select v-model="formInline.parameter" clearable placeholder="">
								<el-option
									v-if="typeList"
									v-for="item in typeList"
									:key="item.id"
									:label="item.name"
									:value="item.id"
									>
								</el-option>
							</el-select>
						</el-form-item>
						<el-form-item label="">
							<el-input v-model="formInline.key" placeholder="" clearable></el-input>
						</el-form-item>
						<el-form-item>
							<el-button type="primary" @click="searchSys" icon="el-icon-search"></el-button>
						</el-form-item>
					</el-form>
					<div class="add-btn">
						<el-button-group class="marl15">
							<el-button type="primary" icon="el-icon-plus" size="small" @click="addParam('addParam')"></el-button>
							<el-button type="danger" icon="el-icon-delete" size="small" @click="delParam('delParam')"></el-button>
						</el-button-group>
						<el-button type="primary" icon="el-icon-circle-plus" size="small" @click="add('addSys')" class="marr20"></el-button>
					</div>
					<el-table
						v-loading='loading'
						v-if="systemList"
						:data="systemList"
						border
						stripe
						max-height="540"
						style="width: 100%">
						<el-table-column
							type="index"
							label=""
							fixed
							align="center"
							header-align="center"
							width="50">
						</el-table-column>
						<el-table-column
							prop="code"
							label=""
							header-align="center"
							align="center"
							width="">
						</el-table-column>
						<el-table-column
							prop="c_name"
							label=""
							header-align="center"
							align="center"
							width="">
						</el-table-column>
						<el-table-column
							prop="remarks"
							label=""
							header-align="center"
							align="center"
							width="">
						</el-table-column>
						<el-table-column
							prop="cur_status"
							label=""
							header-align="center"
							align="center"
							width="">
						</el-table-column>
						<el-table-column 
							label="" 
							width="250"  
							fixed="right" 
							header-align="center" 
							align="center">
							<template slot-scope="scope">
								<el-button type="primary" size="small" @click.stop="edit('editSys',scope.row)"></el-button>
								<el-button type="danger" icon="" size="small" @click.stop="del(scope.row)"></el-button>
							</template>
						</el-table-column>
					</el-table>
					<div class="page page-fixed_">
						<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
							:current-page="elementPageData.pageNum"
							:page-sizes="elementPageData.defaultData.pageSizes"
							:page-size="elementPageData.defaultData.pageSizeDefault"
							background
							small
							layout="total, sizes, prev, pager, next, jumper"
							:total="systemListTotal">
						</el-pagination>
					</div>
				</div>
			</div>
		</div>
		<!-- 
			
		-->
		<el-dialog :title="dialogTitleType=='addSys'?'':''" :visible.sync="dialogAddEditFormVisible" width="650px" center>
			<el-form :model="sysform" ref="sysform" :rules="sysrules" label-width="80px" class="demo-ruleForm" v-loading='loading_'>
				<el-form-item label="" prop="code">
					<el-input v-model="sysform.code"></el-input>
				</el-form-item>
				<el-form-item label="" prop="name">
					<el-input v-model="sysform.name"></el-input>
				</el-form-item>
				<el-form-item label="" prop="type">
					<el-select v-model="sysform.type" placeholder="">
						<el-option
							v-if="typeList"
							v-for="item in typeList"
							:key="item.id"
							:label="item.name"
							:value="item.id"
							>
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="" prop="remarks">
					<el-input v-model.number="sysform.remarks"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button type="primary" @click="addForm('sysform')" v-if="dialogTitleType=='addSys'"> </el-button>
				<el-button type="primary" @click="editForm('sysform')" v-if="dialogTitleType=='editSys'"> </el-button>
				<el-button @click="resetForm('sysform')"> </el-button>
				<el-button @click="dialogAddEditFormVisible = false"> </el-button>
			</div>
		</el-dialog>
		<!-- -->
		<el-dialog
			:title="typeParamsTitle=='delParam'?'':''"
			:visible.sync="paramDialogVisible"
			width="650px">
			<el-form :model="paramForm" ref="paramForm" label-width="150px" class="demo-dynamic" size="middle" v-if="typeParamsTitle=='addParam'">
				<el-form-item
					prop="name"
					label=""
					:rules="[{ required: true, message: '', trigger: 'blur' },]">
					<el-input v-model="paramForm.name"></el-input>
				</el-form-item>
			</el-form>
			<div v-if="typeParamsTitle=='delParam'" v-loading='loading_'>
				<el-table v-if="typeList" :data="typeList" border stripe max-height="560" style="width: 100%">
					<el-table-column
						type="index"
						label=""
						fixed
						align="center"
						header-align="center"
						width="50">
					</el-table-column>
					<el-table-column
						prop="name"
						label=""
						header-align="center"
						align="center"
						width="">
					</el-table-column>
					<el-table-column 
						label="" 
						width="120"  
						fixed="right" 
						header-align="center" 
						align="center">
						<template slot-scope="scope">
							<el-button type="danger" icon="" size="small" @click.stop="del_(scope.row)"></el-button>
						</template>
					</el-table-column>
				</el-table>
			</div>
			<span slot="footer" class="dialog-footer" v-if="typeParamsTitle=='addParam'">
				<el-button @click="paramDialogVisible = false"> </el-button>
				<el-button type="primary" @click="addParamForm('paramForm')"> </el-button>
			</span>
		</el-dialog>
	</div>
</template>

<style>
	
</style>

<style src="../../assets/style/system.less" scoped lang="less"></style>

<script>
	
export default {
	methods: {
		
		//-------------------------------------------------------------------------------------
		/* 
			GET  --
		*/
		getTypeList(){
			this.loading_ = true;
			this.axios.get(`${this.common.basePath}/node/selectNodesType`,this._typeParams()).then( (response) => {
				if(response.data.meta.code === 0){
					this.typeList = response.data.data;//
				}else{
					this.$message.error(response.data.meta.message, 20);   
				}
				this.loading_ = false;
			}).catch( (error) => {
				this.$message.error(error);
				this.loading_ = false;
			});
		},
		_typeParams(){
			return {
				params: {}
			}
		},
		
		//-------------------------------------------------------------------------------------
		/* 
			
		*/
		searchSys(){
			this.elementPageData.pageNum = 1;
			//null
			if(this.formInline.parameter==''){
				this.formInline.parameter=null;
			}
			if(this.formInline.key==''){
				this.formInline.key=null;
			}
			this.getSystemList();
		},
		/* 
			GET  --
		*/
		getSystemList(){
			this.loading = true;
			this.axios.get(`${this.common.basePath}/node/selectNodesIfo`,this._systemParams()).then( (response) => {
				if(response.data.meta.code === 0){
					this.systemList = response.data.data.list;//
					this.systemListTotal = response.data.data.total;//
				}else{
					this.$message.error(response.data.meta.message, 20);   
				}
				this.loading = false;
			}).catch( (error) => {
				this.$message.error(error);
				this.loading = false;
			});
		},
		_systemParams(){
			return {
				params: {
					pageSize: this.elementPageData.pageSize,
					currentPage:  this.elementPageData.pageNum,
					keys:this.formInline.key,
					type_id:this.formInline.parameter,
				}
			}
		},
		
		//-------------------------------------------------------------------------------------
		/*
		* 
		* @param {number} val 
		*/
		handleCurrentChange(val){
			this.elementPageData.pageNum = val;
			this.getSystemList();
		},
		/*
		* 
		* @param {number} val 
		*/
		handleSizeChange(val){
			this.elementPageData.pageSize = val;
			this.getSystemList();
		},
		
		//-------------------------------------------------------------------------------------
		/* 
			
		 */
		add(type){
			//
			this.editId = null;
			//dialogTitleType
			this.dialogTitleType = type;
			//dialogAddEditFormVisible
			this.dialogAddEditFormVisible = true;

			//
			if(this.$refs['sysform']){
				this.$refs['sysform'].resetFields();
			} 
		},
		/* 
		
		*/
		addForm(formName) {
			this.$refs[formName].validate((valid) => {
				if (valid) {
					let addSysForm = {
						code:this.sysform.code,
						name:this.sysform.name,
						type_id:this.sysform.type,
						remarks:this.sysform.remarks,
					};
					this.axios.post(`${this.common.basePath}/node`,addSysForm).then( (response) => {
						if(response.data.meta.code === 0){
							this.$message.success("", 20);
							this.dialogAddEditFormVisible = false
							this.getSystemList();
						}else{
							this.$message.error(response.data.meta.message, 20);   
						}
					}).catch( (error) => {
						this.$message.error(error);
					});
				} else {
					this.$message.error('', 20); 
					return false;
				}
			});
		},
		/* 
			
		*/
		edit(type,colData){
			//
			this.editId = null;
			//dialogTitleType
			this.dialogTitleType = type;
			//
			this.loading_ = true;
			this.axios.get(`${this.common.basePath}/node/${colData.c_id}`,this._typeParams()).then( (response) => {
				if(response.data.meta.code === 0){
					this.sysform = response.data.data;
					this.$set(this.sysform,"type", response.data.data.type_id);
				}else{
					this.$message.error(response.data.meta.message, 20);   
				}
				this.loading_ = false;
			}).catch( (error) => {
				this.$message.error(error);
				this.loading_ = false;
			});
			this.editId = colData.c_id;
			//dialogAddEditFormVisible
			this.dialogAddEditFormVisible = true;

			//
			if(this.$refs['sysform']){
				this.$refs['sysform'].resetFields();
			} 
		},
		/* 
			
		*/
		editForm(formName) {
			this.$refs[formName].validate((valid) => {
				if (valid) {
					// var typeId = '';
					// for(var i = 0;i<this.typeList.length;i++){//type_id
					// 	if(this.typeList[i].id===this.sysform.type){
					// 		typeId = this.typeList[i].id
					// 	}
					// }
					// this.sysform.type = typeId;
					let editSysForm = {
						code:this.sysform.code,
						name:this.sysform.name,
						type_id:this.sysform.type,
						remarks:this.sysform.remarks,
					};
					this.axios.patch(`${this.common.basePath}/node/${this.editId}`,editSysForm).then( (response) => {
						if(response.data.meta.code === 0){
							this.$message.success("", 20);
							this.dialogAddEditFormVisible = false
							this.getSystemList();
						}else{
							this.$message.error(response.data.meta.message, 20);   
						}
					}).catch( (error) => {
						this.$message.error(error);
					});
				} else {
					this.$message.error('', 20); 
					return false;
				}
			});
		},
		/* 
			
		*/
		resetForm(formName) {
			this.$refs[formName].resetFields();
		},
		/* 
			
		*/
		del(colData){
			this.$confirm('', '', {
				confirmButtonText: '',
				cancelButtonText: '',
				type: 'warning'
			}).then(() => {
				this.axios.patch(`${this.common.basePath}/node/delete/${colData.c_id}`,{}).then( (response) => {
					if(response.data.meta.code === 0){
						this.$message({
							type: 'success',
							message: '!'
						});
						this.getSystemList();
					}else{
						this.$message.error(response.data.meta.message, 20);   
					}
				}).catch( (error) => {
					this.$message.error(error);
				});
			}).catch(() => {});
		},
		
		//-------------------------------------------------------------------------------------
		/* 
			
		 */
		addParam(type){
			this.typeParamsTitle = type;
			this.paramDialogVisible = true;
			
			if(this.$refs['paramForm']){
				this.$refs['paramForm'].resetFields();
			}
		},
		/* 
			
		 */
		addParamForm(formName) {
			this.$refs[formName].validate((valid) => {
				if (valid) {
					this.axios.post(`${this.common.basePath}/node/addNodesType`,this.paramForm).then( (response) => {
						if(response.data.meta.code === 0){
							this.$message.success("", 20);
							this.paramDialogVisible = false
							this.getTypeList();
						}else{
							this.$message.error(response.data.meta.message, 20);   
						}
					}).catch( (error) => {
						this.$message.error(error);
					});
				} else {
					return false;
				}
			});
		},
		/* 
			
		*/
		delParam(type){
			this.getTypeList();
			this.typeParamsTitle = type;
			this.paramDialogVisible = true;
		},
		/* 
		 
		 */
		del_(colData){
			this.$confirm('', '', {
				confirmButtonText: '',
				cancelButtonText: '',
				type: 'warning'
			}).then(() => {
				this.axios.get(`${this.common.basePath}/node/deleteNodesType`,{params:{id:colData.id}}).then( (response) => {
					if(response.data.meta.code === 0){
						this.$message({
							type: 'success',
							message: '!'
						});
						this.paramDialogVisible = false
						this.getTypeList();
					}else{
						this.$message.error(response.data.meta.message, 20);   
					}
				}).catch( (error) => {
					this.$message.error(error);
				});
			}).catch(() => {});
		},
	
	},
	mounted(){ 
		/* 
			GET  --
		*/
		this.getSystemList();
		
		/* 
			GET  --
		*/
		this.getTypeList();
	},
	data(){
		return {
			loading:false,//
			loading_:false,//
			dialogTitleType:null,//
			dialogAddEditFormVisible:false,//
			editId:null,//id
			sysform:{//
				code:null,
				name:null,
				type:null,
				remarks:null,
			},
			sysrules:{
				code: [
					{ required: true, message: '', trigger: 'blur' },
				],
				name: [
					{ required: true, message: '', trigger: 'blur' },
				],
				type: [
					{ required: true, message: '', trigger: 'change' }
				],
			},
			
			typeList:null,//
			elementPageData: {               
				//
				defaultData:  this.common.elementPagination,
				pageNum:      this.common.elementPagination.pageNumDefault,
				pageSize:     this.common.elementPagination.pageSizeDefault,
			},
			formInline: {
				key:null,
				parameter:null,
			},
			systemList:null,//
			systemListTotal:null,//
			
			typeParamsTitle:null,//
			paramDialogVisible:false,//
			paramForm:{//
				name:null,
			}
		}
	},
};
</script> 


