		//-------------------------------------------------------------------------------------
		/* 
			tab 
		*/
		dialogHandleClick(tab, event) {
			if(tab.name=="first"){//
				this.loading_ = true;
				this.axios.get(`${this.common.basePath}/user_manage/searchPower`,{params:{}}).then( (response) => {
					if(response.data.meta.code === 0){
						this.authorizeData = response.data.data.list;//
					}else{
						this.$message.error(response.data.meta.message, 20);   
					}
					this.loading_ = false;
				}).catch( (error) => {
					this.$message.error(error);
					this.loading_ = false;
				});
			}else if(tab.name=="second"){//
				this._pagePower();
			}
		},
		_pagePower(){
			this.loading_ = true;
			this.axios.get(`${this.common.basePath}/system_manage/select_tree`,{params:{roleId:this.roleId}}).then( (response) => {
				if(response.data.meta.code === 0){
					this.authorizeData_ = response.data.data;//
					
					let keysArr = [];
					let arr = [];//
					for(var i = 0;i<response.data.data.length;i++){//
						
						if(response.data.data[i].isSelect==true){
							keysArr.push(response.data.data[i].id)//
						}
						
						if(response.data.data[i].childrens.length!=0){
							arr.push(response.data.data[i].childrens)
						}
					}
					
					if(arr.length!=0){
						for(var j = 0;j<arr.length;j++){//
							for(var k = 0;k<arr[j].length;k++){
								if(arr[j][k].isSelect==true){
									keysArr.push(arr[j][k].id)//
								}
							}
						}
					}
					
					//keysArr
					var r = keysArr.filter(function(element,index,self){
						return self.indexOf(element) === index;
					});
					this.defaultKeys = r;
				}else{
					this.$message.error(response.data.meta.message, 20);   
				}
				this.loading_ = false;
			}).catch( (error) => {
				this.$message.error(error);
				this.loading_ = false;
			});
		},
		/* 
		 
		 */
		saveResource(){
			let form = {
				id:this.$refs.tree_.getCheckedKeys(),
				role_id:this.roleId,
			}
			this.axios.post(`${this.common.basePath}/system_manage/upRolePage/${this.roleId}`,form).then( (response) => {
				if(response.data.meta.code === 0){
					this.$message.success("...,!");
					this._pagePower();
				}else{
					this.$message.error(response.data.meta.message, 20);
				}
			}).catch( (error) => {
				this.$message.error(error);
			});
		},
		/* 
		 
		 */
		authorize(data){
			this.dialogActiveName = "first";
			this.roleId = data.id;//id
			this.authorizeDialogVisible = true;
			
			//id
			let powerArr = [];
			for(var i = 0;i<data.power.length;i++){
				powerArr.push(data.power[i].authority_id)
			}
			var powerArr_ = powerArr.filter(function(element,index,self){//
				return self.indexOf(element) === index;
			});
			this.powerArr = powerArr_;
			this.loading_ = true;
			this.axios.get(`${this.common.basePath}/user_manage/searchPower`,{params:{}}).then( (response) => {
				if(response.data.meta.code === 0){
					this.authorizeData = response.data.data.list;//
				}else{
					this.$message.error(response.data.meta.message, 20);   
				}
				this.loading_ = false;
			}).catch( (error) => {
				this.$message.error(error);
				this.loading_ = false;
			});
		},
		/* 
		 
		 */
		isPower(type,data){
			this.axios.get(`${this.common.basePath}/user_manage/addRolesPower`,{params:{role_id:this.roleId,type:type,authority_id:data.id}}).then( (response) => {
				if(response.data.meta.code === 0){
					// 
					if(type=='1'){
						this.powerArr.push(data.id);
						var powerArr_ = this.powerArr.filter(function(element,index,self){//
							return self.indexOf(element) === index;
						});
						this.powerArr = powerArr_;
						this.$message.success("");
						this.getRoleList();
					}else if(type=='0'){
						let index = "";
						for(var i = 0;i<this.powerArr.length;i++){//
							if(this.powerArr[i]==data.id){
								index=i
							}
						}
						this.powerArr.splice(index,1);
						this.$message.success("");
						this.getRoleList();
					}
					
				}else{
					this.$message.error(response.data.meta.message, 20);   
				}
			}).catch( (error) => {
				this.$message.error(error);
			});
		},
		
	},
	mounted(){ 
		/* 
		tabTwoPages
		*/
		let userPages = [];
		for(var i = 0;i<this.tabTwoPages.length;i++){
			if(this.tabTwoPages[i].split("_")[0]=="user"){
				userPages.push(this.tabTwoPages[i])
			}
		}
		this.userPages = userPages;
		this.activeName = userPages[0];
		
		/* 
			GET  --
		*/
		this.getUserList();
		
		/* 
			GET  --
		*/
		this.getRoleList();
	},
	data(){
		return {
			loading:false,//
			loading_:false,//
			loading_role:false,//

			userPages:[],//
			dialogActiveName:'first',
			activeName:null,
			elementPageData: {               
				//
				defaultData:  this.common.elementPagination,
				pageNum:      this.common.elementPagination.pageNumDefault,
				pageSize:     this.common.elementPagination.pageSizeDefault,
			},
			formInline: {
				name: null,
				account : null,
				role:null,
				username:null,
				code:null,
				author:null,
				path:null,
			},
			rose_id:null,//id
			userList: null,//
			userListTotal:null,//
			roleList:null,//
			roleListTotal:null,//
			roleUserList:[],//-
			roleUserListId:null,//id
			powerList:null,//
			powerListTotal:null,//
			
			dialogAddFormVisible:false,//
			dialogTitleIndex:null,
			dialogTitleArr:['','','','','','','','',"",""],
			editId:null,//id
			addform:{//
				account:null,
				name:null,
				password:null,
				phone:null,
				telephone:null,
				email:null,
				role_code:null,
				role_name:null,
				role_remarks:null,
				jurisdiction_name:null,
				jurisdiction_path:null,
				jurisdiction_type:null,
				jurisdiction_remarks:null,
				page_name:null,
				page_code:null,
			},
			addrules:{
				account: [
					{ required: true, message: '', trigger: 'blur' },
					{	pattern: /^[a-zA-Z0-9_-]{1,16}$/, message: '' },
				],
				name: [
					{ required: true, message: '', trigger: 'blur' },
					{	pattern:  /^[\u4E00-\u9FA5\uf900-\ufa2ds]{2,20}$/, message: '' },
				],
				password: [
					{ required: true, message: '', trigger: 'blur' },
					{	pattern: /^[\a-zA-Z0-9_-]{3,16}$/, message: '' },
				],
				phone: [
					{ required: true, message: '', trigger: 'blur' },
					{ type: 'number', message: ''},
				],
				telephone: [
					{ required: true, message: '', trigger: 'blur' },
					{ min: 11, max: 11, message: '11', trigger: 'blur' },
					{	pattern: /(^1[3|4|5|7|8]\d{9}$)|(^09\d{8}$)/, message: '' }
				],
				role_code: [
					{ required: true, message: '', trigger: 'blur' },
					{ min: 1, max: 25, message: ' 1  25 ', trigger: 'blur' },
				],
				role_name: [
					{ required: true, message: '', trigger: 'blur' },
					{ min: 1, max: 25, message: ' 1  25 ', trigger: 'blur' },
				],
				jurisdiction_path: [
					{ required: true, message: '', trigger: 'blur' },
				],
				jurisdiction_type: [
					{ required: true, message: '', trigger: 'change' }
				],
				jurisdiction_name: [
					{ required: true, message: '', trigger: 'blur' },
					{ min: 1, max: 30, message: ' 1  30 ', trigger: 'blur' },
				],
				page_name: [
					{ required: true, message: '', trigger: 'blur' },
					{ min: 1, max: 8, message: ' 1  8 ', trigger: 'blur' },
				],
				page_code: [
					{ required: true, message: '', trigger: 'blur' },
				],
			},
			addPageDialogVisible:false,//
			addPageForm:{//
				name:null,
				code:null,
				parent_id:null,
			},
			
			authorizeDialogVisible:false,//
			authorizeData: null,//
			authorizeDefaultProps: {
				children: 'children',
				label: 'name'
			},
			authorizeData_: null,//
			authorizeDefaultProps_: {
				children: 'childrens',
				label: 'code'
			},
			defaultKeys:[],//
			tempAtId:null,//Id
			powerArr:[],//id
			roleId:null,//id
			
			pageResourse:null,//
			defaultPageProps: {
				children: 'children',
				label: 'name'
			},
		}
	},
};
</script> 


